.table-extend-right-menu {
  min-width: 72px;
  height: 28px;
  line-height: 28px;
  font-size: 13px;
  padding: 0 10px;
  cursor: pointer;
}
.table-extend-right-menu:hover {
  background: #FBE2CF;
}
.table-extend-right-menu:hover .u-form-control-wrapper {
  background: #FBE2CF;
}
.table-extend-right-menu .include-child-selected {
  color: #e14c46;
}
.table-extend-right-menu.include-child {
  border-top: 1px solid #ECECEC;
}
.table-extend-right-menu.insert div {
  display: inline-block;
}
.table-extend-right-menu.insert div .insert_number {
  width: 36px;
  height: 22px;
  border-radius: 1px;
  margin-left: 10px;
}
.table-extend-right-menu .u-form-control-wrapper {
  height: 24px;
  line-height: 24px;
}
.table-extend-right-menu .u-form-control-wrapper:hover {
  background: #FBE2CF;
}
#nc-table-setColumnMenu .rightMenu .table-extend-right-menu.table-menus-sep {
  -webkit-box-sizing: content-box;
          box-sizing: content-box;
  margin-bottom: 10px;
}
#nc-table-setColumnMenu .rightMenu .table-extend-right-menu.table-menus-sep:after {
  content: "";
  display: block;
  height: 5px;
  border-bottom: 1px solid #E4E4E4;
  margin: 0 -10px;
}
#table-copy-element {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  z-index: -10;
}
