.table-cell-drag-icon {
  position: absolute;
  right: 1px;
  bottom: 1px;
  width: 9px;
  overflow: hidden;
  height: 9px;
  cursor: url(./cell-drag-batch.cur) 9 9, cell;
  z-index: 6;
}
.table-cell-drag-icon::after {
  content: "";
