.nc-table .u-table-footer {
  padding: 0px !important;
  border-bottom: 1px solid #c1c7d0;
}
.nc-table .u-table-footer .total-row tr td {
  background-color: #fffddd;
  color: #555;
  font-weight: bold;
  border-bottom: none;
}
.nc-table .u-table-footer .u-table.total-row {
  margin: 0px;
}
.nc-table .u-table-footer .u-table.total-row div {
  background-color: #fffddd;
}
.nc-table .u-table-footer .u-table.total-row .u-table-fixed-left,
.nc-table .u-table-footer .u-table.total-row .u-table-fixed-right {
  -webkit-box-shadow: none;
          box-shadow: none;
}
.nc-table .u-table-footer .u-table.total-row .u-table-fixed-left {
  min-width: 50px;
}
.nc-table .u-table-footer .u-table.total-row .u-table-body {
  overflow: auto !important;
}
.nc-table .u-table-footer .u-table.total-row .u-table-body-outer .u-table-body-inner {
  overflow: hidden;
}
.nc-table.u-table .u-table-footer .total-row .u-table-content .u-table-tbody tr td {
  border-right: 1px solid #fffddd;
}
.nc-table.u-table .u-table-footer .total-row .u-table-content .u-table-fixed-right .u-table-tbody tr td {
  border-left: 1px solid #fffddd;
}
.nc-table.u-table .u-table-footer .total-row .u-table-fixed-left tr td:first-child {
  padding-left: 20px !important;
}
.total-row-table-cell .ellipsis {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  display: block;
}
.tooltip-word-color .tooltip-inner {
  color: #555555 !important;
  background-color: #ffffff !important;
  -webkit-box-shadow: 0px 3px 12px 0px rgba(93, 74, 74, 0.5);
          box-shadow: 0px 3px 12px 0px rgba(93, 74, 74, 0.5);
}
.tooltip-word-color .tooltip-inner a,
.tooltip-word-color .tooltip-inner span {
  color: #555555;
  text-decoration: none;
}
