.pagination-wrapper {
  height: 42px;
  padding: 0px 20px;
  border-top: 1px solid #e4e4e4;
  overflow: hidden;
  background-color: #eee;
  border-radius: 0 0 3px 3px;
}
.pagination-wrapper .pagination-size {
  float: left;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  height: 100%;
}
.pagination-wrapper .pagination-size .pagination-select {
  width: 100px;
}
.pagination-wrapper .pagination-size .pagination-total {
  line-height: 42px;
  margin-left: 10px;
  font-size: 13px;
  color: #555;
  vertical-align: bottom;
}
.pagination-wrapper .pagination {
  float: right;
}
.pagination-select-dropdown .pagination-option {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  height: 27px;
  padding: 7px 16px 7px 6px;
}
.pagination-select-dropdown .pagination-option .pagination-size {
  text-overflow: ellipsis;
  -o-text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.pagination-tip {
  z-index: 1801 !important;
}
