@import "../../../../../static/font/iconfont.css";
.ui-nc-header-warp {
  color: #e3e7ed;
}
.u-table.nc-table th .th-filter {
  font-size: 14px;
  font-weight: normal;
  position: absolute;
  right: 20px;
  background-color: #F0F4FB;
  width: 26px;
  padding-left: 4px;
  display: none;
  top: 50%;
  height: 16px;
  margin-top: -8px;
  text-align: left;
  color: #4b4b4b;
}
.u-table.nc-table th .bee-table-column-sorter {
  margin-top: -8px;
  position: absolute;
  right: 1px;
  padding-right: 4px;
  top: 50%;
  background-color: #F0F4FB;
}
.u-table.nc-table th .bee-table-column-sorter .bee-table-column-sorter-flat {
  display: none;
}
.u-table.nc-table th .bee-table-column-sorter .bee-table-column-sorter-flat i.uf.uf-symlist,
.u-table.nc-table th .bee-table-column-sorter i {
  color: #4b4b4b;
  background-color: #F0F4FB;
}
.u-table.nc-table th.column-type-number .bee-table-column-sorter {
  position: absolute;
  top: 50%;
  left: 0;
  right: auto;
  margin-top: -8px;
}
.u-table.nc-table th.column-type-number .th-filter {
  left: 20px;
  right: auto;
}
.u-table.nc-table th span:hover .th-filter,
.u-table.nc-table th .th-filter.filtered {
  display: inline-block;
}
.u-table.nc-table th span:hover .bee-table-column-sorter .bee-table-column-sorter-flat {
  display: block;
}
.u-table.nc-table th .icon-lieguolv {
  cursor: pointer;
}
.u-table.nc-table th .icon-lieshaixuanx {
  cursor: pointer;
  color: #ff6565;
}
td.column-type-number .table-cell-wrapper .cell-content.number-right {
  padding-right: 7px;
}
td.column-type-number .table-cell-wrapper.table-cell-focus-blue .cell-content {
  padding-right: 0;
}
.nc-modal.u-modal.filterPanel-modal .u-modal-dialog {
  min-width: 680px;
  max-width: 680px;
}
.nc-modal.u-modal.filterPanel-modal .u-modal-dialog .u-modal-content {
  height: unset;
  min-height: 420px;
}
.nc-modal.u-modal.filterPanel-modal .u-modal-dialog .u-modal-content .u-modal-body .filter-item-container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-top: 10px;
}
.nc-modal.u-modal.filterPanel-modal .u-modal-dialog .u-modal-content .u-modal-body .filter-item-container .select-container {
  width: 180px;
  margin-left: 40px;
}
.nc-modal.u-modal.filterPanel-modal .u-modal-dialog .u-modal-content .u-modal-body .filter-item-container .input-container {
  width: 329px;
  margin-left: 12px;
}
.nc-modal.u-modal.filterPanel-modal .u-modal-dialog .u-modal-content .u-modal-body .filter-item-container .icon-shanchu {
  font-size: 20px;
  line-height: 30px;
  margin-left: 6px;
}
.nc-modal.u-modal.filterPanel-modal .u-modal-dialog .u-modal-content .u-modal-body .filter-item-container .icon-shanchu:hover {
  color: #e14c46;
}
.nc-modal.u-modal.filterPanel-modal .u-modal-dialog .u-modal-content .u-modal-body .add-btn-container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-top: 10px;
}
.nc-modal.u-modal.filterPanel-modal .u-modal-dialog .u-modal-content .u-modal-body .add-btn-container .add-btn {
  margin-left: 40px;
  width: 455px;
  height: 30px;
  line-height: 30px;
  text-align: center;
  border: 1px dashed;
  border-radius: 3px;
  border-color: #dddddd;
}
.nc-modal.u-modal.filterPanel-modal .u-modal-dialog .u-modal-content .u-modal-footer .u-button-group .u-button.filter-btn.nc-button-wrapper {
  min-width: 60px;
}
.nc-modal.u-modal.filterPanel-modal .u-modal-dialog .u-modal-content .u-modal-footer .u-button-group .u-button.reset-btn.nc-button-wrapper {
  min-width: 60px;
}
.table-filter-tips {
  height: 30px;
  background: #fffde9;
  border-radius: 2px;
  border: 1px solid #fbe3ae;
  display: inline-block;
  line-height: 30px;
  vertical-align: middle;
  font-weight: 400;
  color: #9b6d4e;
  margin-left: 5px;
  padding: 0 8px;
  width: 252px;
}
.filter-tips-hidden {
  display: none;
}
.u-modal-open .nc-modal.u-modal .u-modal-dialog .u-modal-content .u-modal-header .u-modal-title .iconfont.icon.icon-gantanhao {
  color: #a07457;
  font-size: 14px;
  width: 14px;
  height: 14px;
  line-height: 14px;
  margin-right: 7px;
  position: relative;
  top: 0;
}
